import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from 'src/app/adminPanel/admin.service';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {

  userData: any = [];
  id: any;
  name: any;
  fname: any;
  email: any;
  company: any;
  dob: any;
  gender: any;
  plan: any;
  for: any;
  mobileno: any;
  pic: any;
  cnic: any;

  constructor(private router: Router, private activeRoute: ActivatedRoute, private service: AdminService) { }

  ngOnInit() {
    this.activeRoute.params.subscribe(data => {
      this.service.getOneUser(data['id']).subscribe(data => {
        this.userData = data;
      });
  });
  }
  myform(fullform) {
    console.log(fullform.value);
    this.id = this.activeRoute.snapshot.params.id;
    this.service.updateUser(this.id, fullform.value).subscribe(data => { this.router.navigate(['/admin']);
  });
  }
  }
