import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { UsersModel } from './Models/usersModel';
import { CompanyModel } from './Models/companyModel';
import { ContactMessageModel } from './Models/contactMessageModel';
import { ClaimModel } from './Models/claimModel';
import { CombineModel } from './Models/combineModel';

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  usrModel: UsersModel [] = [];
  companyModel: CompanyModel [] = [];
  contactMessageModel: ContactMessageModel [] = [];
  claimModel: ClaimModel [] = [];
  combineModel: CombineModel [] = [];

  private getUsersUrl = 'http://localhost:7000/admin/allUsers';
private userDeleteUrl = 'http://localhost:7000/admin/delete/';
private userEditUrl = 'http://localhost:7000/admin/editUser/';
private companyUrl = 'http://localhost:7000/admin/company';
private messageUrl = 'http://localhost:7000/admin/contact';
private totalUsersUrl = 'http://localhost:7000/admin/countAll';
private totalCompaniesUrl = 'http://localhost:7000/admin/allCompanies';
private totalClaimRequestsUrl = 'http://localhost:7000/admin/tClaimRequests';
private delCompanyUrl = 'http://localhost:7000/admin/delCompany/';
private lifeInsuranceUsersUrl = 'http://localhost:7000/admin/lifeInsuranceUsers';
private investmentUsersUrl = 'http://localhost:7000/admin/investmentUsers';
private homeInsuranceUrl = 'http://localhost:7000/admin/homeInsurUsers';
private healthInsuranceUrl = 'http://localhost:7000/admin/healthInsurUsers';
private claimRequestUrl = 'http://localhost:7000/admin/claim';
private updateUserUrl = 'http://localhost:7000/admin/updateUser/';
private getLifeInsurenceRequestsUrl = 'http://localhost:7000/users//lifeRGet';
private getHealthInsurenceRequestsUrl = 'http://localhost:7000/users//healthRGet';
investPlanUrl = 'http://localhost:7000/users/investRGet';

  constructor(private http: HttpClient) { }

  getUsers(): Observable<UsersModel> {
    return this.http.get<UsersModel>(this.getUsersUrl);
  }
  deleteUser(id) {
    console.log('user id in class : ' + id);
    return this.http.delete(this.userDeleteUrl + id);
  }
  getOneUser(usrid) {
    return this.http.get(this.userEditUrl + usrid);
  }
  updateUser(id, data) {
    console.log(data);
    return this.http.put(this.updateUserUrl + id, data);
  }
  allCompanies(): Observable<CompanyModel> {
    return this.http.get<CompanyModel>(this.companyUrl);
  }
  delCompany(id) {
    return this.http.delete(this.delCompanyUrl + id);
  }
  contactMessages(): Observable<ContactMessageModel> {
    return this.http.get<ContactMessageModel>(this.messageUrl);
  }
  totalUsers() {
    return this.http.get(this.totalUsersUrl);
  }
  totalCompanies() {
    return this.http.get(this.totalCompaniesUrl);
  }
  totalClaimRequests() {
    return this.http.get(this.totalClaimRequestsUrl);
  }
  lifeInsurance(): Observable<UsersModel> {
    return this.http.get<UsersModel>(this.lifeInsuranceUsersUrl);
  }
  homeInsurance(): Observable<UsersModel> {
    return this.http.get<UsersModel>(this.homeInsuranceUrl);
  }
  healthInsurance(): Observable<UsersModel> {
    return this.http.get<UsersModel>(this.healthInsuranceUrl);
  }
  investment(): Observable<UsersModel> {
    return this.http.get<UsersModel>(this.investmentUsersUrl);
  }
  claimRequest(): Observable<ClaimModel> {
    return this.http.get<ClaimModel>(this.claimRequestUrl);
  }
  lifeIRequets(): Observable<CombineModel> {
    return this.http.get<CombineModel>(this.getLifeInsurenceRequestsUrl);
  }
  investIRequests(): Observable<CombineModel> {
    return this.http.get<CombineModel>(this.investPlanUrl);
  }
  healthIRequests(): Observable<CombineModel> {
    return this.http.get<CombineModel>(this.getHealthInsurenceRequestsUrl);
  }

}
