import { Component, OnInit, Input } from '@angular/core';
import * as Highcharts from 'highcharts';

@Component({
  selector: 'app-users-chart',
  templateUrl: './users-chart.component.html',
  styleUrls: ['./users-chart.component.css']
})
export class UsersChartComponent implements OnInit {

  @Input() data = [];
  chartOptions: {};
  Highcharts = Highcharts;

  constructor() { }

  ngOnInit() {
    this.chartOptions = {

      title: {
        text: 'Users Growth by Sector, 2020'
      },
      subtitle: {
        text: 'www.policydukaan.com'
      },
      credits: {
        enabled: false
      },
      legend: {
        layout: 'vertical',
        align: 'right',
        verticalAlign: 'middle'
      },
      plotOptions: {
        series: {
          label: {
            connectorAllowed: false
          },
          pointStart: 1
        }
      },
      series: this.data,
      responsive: {
        rules: [{
          condition: {
            maxWidth: 500
          },
          chartOptions: {
            legend: {
              layout: 'horizontal',
              align: 'center',
              verticalAlign: 'bottom'
            }
          }
        }]
      }
    };
    setTimeout(() => {
      window.dispatchEvent(
        new Event('resize')
    );
    }, 300);
  }

}
