import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ClaimComponent } from './claim/claim.component';
import { ContactusComponent } from './contactus/contactus.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { LifeinsuranceComponent } from './lifeinsurance/lifeinsurance.component';
import { InvestmentplanComponent } from './investmentplan/investmentplan.component';
import { HomeInsuranceComponent } from './home-insurance/home-insurance.component';
import { HealthInsuranceComponent } from './health-insurance/health-insurance.component';
import { CompanyloginComponent } from './companyregister/companylogin.component';
import { UsrloginComponent } from './usrlogin/usrlogin.component';
import { ComploginComponent } from './complogin/complogin.component';
import { AdminRegisterComponent } from './admin-register/admin-register.component';
import { AdminLoginComponent } from './admin-login/admin-login.component';
import { DefaultComponent } from './adminPanel/default/default.component';
import { DashboardComponent } from './adminPanel/components/dashboard/dashboard.component';
import { AllUsersComponent } from './adminPanel/components/all-users/all-users.component';
import { AdminProfileComponent } from './adminPanel/components/admin-profile/admin-profile.component';
import { CompaniesComponent } from './adminPanel/components/companies/companies.component';
import { HealthInsuranceUsersComponent } from './adminPanel/components/health-insurance-users/health-insurance-users.component';
import { HomeInsuranceUsersComponent } from './adminPanel/components/home-insurance-users/home-insurance-users.component';
import { InvestmentUsersComponent } from './adminPanel/components/investment-users/investment-users.component';
import { LifeInsuranceUsersComponent } from './adminPanel/components/life-insurance-users/life-insurance-users.component';
import { MessagesComponent } from './adminPanel/components/messages/messages.component';
// tslint:disable-next-line: max-line-length
import { HealthInsuranceRequestComponent } from './adminPanel/components/Policies/health-insurance-request/health-insurance-request.component';
import { HomeInsuranceRequestComponent } from './adminPanel/components/Policies/home-insurance-request/home-insurance-request.component';
import { InvestmentRequestComponent } from './adminPanel/components/Policies/investment-request/investment-request.component';
import { LifeInsuranceRequestComponent } from './adminPanel/components/Policies/life-insurance-request/life-insurance-request.component';
import { SettingsComponent } from './adminPanel/components/settings/settings.component';
import { ClaimRequestsComponent } from './adminPanel/components/claim-requests/claim-requests.component';
import { EditUserComponent } from './adminPanel/components/EditForms/edit-user/edit-user.component';


const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'home', component: HomeComponent},
  {path: 'aboutus', component: AboutusComponent},
  {path: 'claim', component: ClaimComponent},
  {path: 'contactus', component: ContactusComponent},
  {path: 'login', component: LoginComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'lifeinsurance', component: LifeinsuranceComponent},
  {path: 'investmentplan', component: InvestmentplanComponent},
  {path: 'homeinsurance', component: HomeInsuranceComponent},
  {path: 'healthinsurance', component: HealthInsuranceComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'companyRegister', component: CompanyloginComponent},
  {path: 'userLogin', component: UsrloginComponent},
  {path: 'compLogin', component: ComploginComponent},
  {path: 'adminRegister', component: AdminRegisterComponent},
  {path: 'adminLogin', component: AdminLoginComponent},
  {path: 'editUser/:id', component: EditUserComponent},
  {
    path: 'admin', component: DefaultComponent,
    children: [
      {path: '', component: DashboardComponent},
      {path: 'allusers', component: AllUsersComponent},
      {path: 'aProfile', component: AdminProfileComponent},
      {path: 'companies', component: CompaniesComponent},
      {path: 'healthIU', component: HealthInsuranceUsersComponent},
      {path: 'homeIU', component: HomeInsuranceUsersComponent},
      {path: 'investmentIU', component: InvestmentUsersComponent},
      {path: 'lifeIU', component: LifeInsuranceUsersComponent},
      {path: 'messages', component: MessagesComponent},
      {path: 'healthIR', component: HealthInsuranceRequestComponent},
      {path: 'homeIR', component: HomeInsuranceRequestComponent},
      {path: 'investmentIR', component: InvestmentRequestComponent},
      {path: 'lifeIR', component: LifeInsuranceRequestComponent},
      {path: 'settings', component: SettingsComponent},
      {path: 'claimR', component: ClaimRequestsComponent},
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
