import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../admin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-investment-users',
  templateUrl: './investment-users.component.html',
  styleUrls: ['./investment-users.component.css']
})
export class InvestmentUsersComponent implements OnInit {

  private users: any = [];

  constructor(private service: AdminService, private router: Router) { }

  ngOnInit() {
    this.service.investment().subscribe(data => console.log(this.users = data));
  }
  deleteUser(id) {
    console.log('user id in service : ' + id);
    this.service.deleteUser(id).subscribe( data => { this.router.navigate(['./']);
   });
  }
}
