import { Component, OnInit } from '@angular/core';
import { MainService } from '../main.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-usrlogin',
  templateUrl: './usrlogin.component.html',
  styleUrls: ['./usrlogin.component.css']
})
export class UsrloginComponent implements OnInit {

  constructor(private service: MainService, private router: Router) { }

  ngOnInit() {
  }
  loginform(fullform) {
    console.log(fullform.value);
    this.service.uLogin(fullform.value).subscribe(
      res => {
        console.log(res.token);
        localStorage.setItem('token', res.token);
        this.router.navigate(['/userDash']);
      }, err => console.log(err)
    )
  }
}
