import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ChartService {

  private totalUsersUrl = 'http://localhost:7000/admin/countAll';
  private totalCompaniesUrl = 'http://localhost:7000/admin/allCompanies';
  private totalClaimRequestsUrl = 'http://localhost:7000/admin/tClaimRequests';
  private totalMessagesUrl = 'http://localhost:7000/admin/tMessages';

  constructor(private http: HttpClient) { }

  totalUsers() {
    return this.http.get(this.totalUsersUrl);
  }
  totalCompanies() {
    return this.http.get(this.totalCompaniesUrl);
  }
  totalClaimRequests() {
    return this.http.get(this.totalClaimRequestsUrl);
  }
  totalMessages() {
    return this.http.get(this.totalMessagesUrl);
  }
}
